<template>
  <div class="playlyric">
    <ul ref="lyricTem" v-if="songLyrics?.length" @mousewheel="xxx">
      <li
        ref="li"
        v-for="(item, index) in songLyrics"
        :key="index"
        :class="{
          load:
            currentTime > item.time &&
            (index == songLyrics.length - 1
              ? true
              : currentTime < songLyrics[index + 1]?.time),
        }"
        :style="
          currentTime > item.time &&
          (index == songLyrics.length - 1
            ? true
            : currentTime < songLyrics[index + 1]?.time) &&
          `animation-duration: ${meiyiju}s;`
        "
      >
        {{ item.text || "..." }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    lyricID: [Number, String],
    currentTime: Number,
  },
  data() {
    return {
      songLyrics: null,
      reveLyric: null,
      indexState: true,
      a: true,
      meiyijudata: 0,
      liHeight: 0,
    };
  },
  computed: {
    // liHeight() {
    //   // if (this.$refs.li) {
    //   return this.$refs?.li[0]?.offsetHeight;
    //   // }
    // },
    meiyiju() {
      return (
        this.songLyrics[
          this.meiyijudata + 1 == this.songLyrics.length
            ? this.songLyrics.length
            : this.meiyijudata + 1
        ]?.time - this.songLyrics[this.meiyijudata]?.time
      );
    },
  },
  methods: {
    //获取歌词
    getLyric(id) {
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: {
            id,
          },
        })
        .then(async (res) => {
          var tmpLyrics = res.data.lrc.lyric.split("\n");
          this.songLyrics = tmpLyrics
            .filter((o) => o)
            .map((o) => {
              try {
                var regex = /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/;
                var { groups } = o.match(regex);
                return {
                  time: groups.mm * 60 + groups.ss * 1,
                  text: groups.text.trim(),
                };
              } catch {
                return "";
              }
            })
            .filter((e) => e !== "");
          this.reveLyric = this.songLyrics.slice().reverse();
          await this.$nextTick();
          // console.log(this.$refs?.li[0],this.$refs?.li[0]?.offsetHeight);
          this.liHeight = this.$refs?.li[0]?.offsetHeight;
        });
    },
    xxx() {
      this.a = false;
      clearTimeout(this.time);
      this.time = setTimeout(() => {
        this.a = true;
      }, 2000);
    },
  },
  created() {
    this.getLyric(this.lyricID);
  },
  watch: {
    currentTime(n) {
      // console.log(n);
      if (
        this.indexState &&
        typeof this.$refs.lyricTem?.scrollTop == "number" &&
        this.a
      ) {
        let i = this.reveLyric?.findIndex((e) => e.time <= n);
        this.meiyijudata = this.reveLyric?.length - 1 - i;
        this.$refs.lyricTem.scrollTop =
          (this.reveLyric?.length - 1 - i) * this.liHeight;
        console.log(this.liHeight);
      }
    },
    lyricID(n) {
      this.getLyric(n);
      // console.log(n);
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes scan {
  0% {
    background-size: 0 100%;
  }
  100% {
    background-size: 100% 100%;
  }
}
.playlyric {
  padding: 40rem 80rem 20rem;
  overflow: hidden;
  height: 55vh;
  ul {
    height: 60vh;
    overflow-y: auto;
    scrollbar-width: none;
    padding: 30vh 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    &::-webkit-scrollbar {
      display: none;
    }
    li {
      flex-shrink: 0;
      flex-grow: 0;
      text-align: center;
      font-size: 14rem;
      padding: 5rem 0;
      height: 46rem;
      display: inline-block;
      background: hsla(0, 0%, 100%, 0.6) -webkit-linear-gradient(
          left,
          #ee6516,
          #ff0404
        ) no-repeat 0 0;
      -webkit-text-fill-color: transparent;
      -webkit-background-clip: text;
      background-size: 0 100%;
      white-space: nowrap;
      &.load {
        background-size: 100% 100%;
        animation: scan ease-out;
        animation-delay: -0s;
      }
    }
  }
}
</style>