<script>
import TheLyrics from "@/components/TheLyrics.vue";
export default {
  components: {
    TheLyrics,
  },
  props: [
    "res",
    "duration",
    "currentTime",
    "autoplay",
    "list",
    "showView",
    "model",
    "thelyric",
  ],
  data: function () {
    return {
      // minute: null,
      // second: null,
      listCard: false,
      draging: false,
      tmpCurrentTime: this.currentTime,
      showlyric: false,
      // countTime: Number,
    };
  },
  methods: {
    shanchu: function (n) {
      this.$emit(
        "del-list",
        this.list.filter((e) => {
          return !(e.id == n.id);
        })
      );
    },
    jindutiao(n) {
      // console.log(n, this.draging);
      this.tmpCurrentTime = n;
    },
  },
  computed: {
    inputTime() {
      return (this.tmpCurrentTime / this.duration) * 75 + "%";
    },
  },
  // created: {
  //   countTime: function (duration) {
  //     return (
  //       Math.floor((duration % 3600) / 60) +
  //       ":" +
  //       Math.floor((duration % 3600) % 60)
  //     );
  //   },
  // },
  watch: {
    currentTime: function () {
      if (!this.draging) {
        this.tmpCurrentTime = this.currentTime;
      }
      //   console.log(this.tmpCurrentTime);
      //   console.log(this.currentTime);
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes yuan {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playview {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  //   background-color: rgb(255, 0, 230);
  //   overflow: hidden;
  //   background-position: center;
  //   background-size: cover;
  //   pointer-events: none;
  z-index: 5;
  .big {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    z-index: 5;
    background-color: rgb(38, 38, 38);
    // pointer-events: none;
    &::after {
      width: 100vw;
