import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AblumView from "../components/AblumView.vue";


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: HomeView,
    meta: { showView: true }
    // children: [
    // ]
  },
  {
    path: '/hot',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/HotView.vue'),
    meta: { showView: true }
  },
  {
    path: '/search',
    component: () => import('../views/SearchView.vue'),
    meta: { showView: true },
  },
  {
    path: '/songCard',
    component: () => import('../components/SongCard.vue'),
    meta: { showView: false }
  },
  {
    path: '/ablum',
    component: AblumView,
    meta: { showView: false }
  },
  {
    path: '/artist',
    component: () => import('../components/ArtistView.vue'),
    meta: { showView: false }
  },
  // {
  //   path: '/search/ablum',
  //   component: () => import('../components/AblumView.vue'),
  //   meta: { showView: false }
  // },
  // {
  //   path: '/search/artist',
  //   component: () => import('../components/ArtistView.vue'),
  //   meta: { showView: false }
  // },
]

const router = new VueRouter({
  routes
})

export default router
