<template>
  <div class="album">
    <div class="bg">
      <span @click="$router.push({ path: '/' })" class="fanhui"
        ><i class="fa fa-chevron-down"></i
      ></span>
      <span @click="$router.push({ path: '/' })" class="fanhui"
        ><i class="fa fa-chevron-down"></i
      ></span>
      <div class="mask">
        <div class="mark-box"></div>
        <p class="red">专辑</p>
        <img :src="album?.blurPicUrl" alt="" />
        <img
          src="//s3.music.126.net/mobile-new/img/album@2x.png?4d5d63fbc459c8e2f7dddead07514fba="
          class="albumbg"
        />
      </div>
      <div></div>
      <div class="mark" :style="bg"></div>
    </div>
    <div class="context" :class="{ act: bool }">
      <p>简历:{{ text }}</p>
      <span @click="bool = !bool"
        ><i v-if="!bool" class="fa fa-angle-down"></i
        ><i v-else class="fa fa-angle-up"></i
      ></span>
      <p v-for="(item, i) in context" :key="i">{{ item }}</p>
    </div>
    <div class="box">
      <div
        v-for="(item, index) in song"
        :key="item.id"
        @click="$emit('playsong', item), $emit('musiclist', song)"
      >
        <div>
          <span> {{ index + 1 }}</span>
        </div>
        <div class="gray">
          <div>
            <p>{{ item.name }}</p>
            <p>{{ item.ar[0].name }}-{{ item.al.name }}</p>
          </div>
          <!-- <div class="bofang"></div> -->
          <!-- <div class="newsong-play"></div> -->
          <div class="play"></div>
        </div>
        <!-- <li>{{ item.name }}</li> -->
      </div>
    </div>

    <div class="comments">
      <h3 class="h3">最新评论</h3>
      <div v-for="item in comments.comments" :key="item.commentId" class="user">
        <div>
          <img
            :src="`${
              item.user?.avatarUrl ? item.user.avatarUrl : ''
            }?imageView=1&type=webp&thumbnail=30x0`"
            alt=""
          />
        </div>
        <div class="userbox">
          <div class="username">
            <p>
              <span>{{ item.user.nickname }}</span
              ><img
                :src="`${
                  item.user?.vipRights?.associator
                    ? item.user.vipRights.associator.iconUrl
                    : ''
                }?imageView=1&type=webp&thumbnail=30x0`"
                alt=""
              />
            </p>
            <span class="give" @click="item.likedCount++"
              >{{ item.likedCount }}
              <i class="fa fa-thumbs-o-up" aria-hidden="true"></i
            ></span>
          </div>
          <p class="time">{{ item.timeStr }}</p>
          <p>{{ item.content }}</p>
          <!-- <p>{{ item.content }}</p> -->
        </div>
      </div>
    </div>
    <div class="comments">
      <h3 class="h3">精彩评论</h3>
      <div
        v-for="item in comments.hotComments"
        :key="item.commentId"
        class="user"
      >
        <div>
          <img
            :src="`${
              item.user?.avatarUrl ? item.user.avatarUrl : ''
            }?imageView=1&type=webp&thumbnail=30x0`"
            alt=""
          />
        </div>
        <div class="userbox">
          <div class="username">
            <p>
              <span>{{ item.user.nickname }}</span
              ><img
                :src="`${
                  item.user?.vipRights?.associator
                    ? item.user.vipRights.associator.iconUrl
                    : ''
                }?imageView=1&type=webp&thumbnail=30x0`"
                alt=""
              />
            </p>
            <span class="give" @click="item.likedCount++"
              >{{ item.likedCount }}
              <i class="fa fa-thumbs-o-up" aria-hidden="true"></i
            ></span>
          </div>
          <p class="time">{{ item.timeStr }}</p>
          <p>{{ item.content }}</p>
        </div>
      </div>
    </div>
    <div class="all">查看全部{{ comments.total }}评论></div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      album: null,
      bool: false,
      bg: {},
      comments: [],
      song: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    console.log(to.query.id);
    next((vm) => {
      vm.axios
        .get("https://apis.netstart.cn/music/album", {
          params: {
            id: to.query.id,
          },
        })
        .then((res) => {
          // console.log(res);
          vm.album = res.data.album;
          vm.bg = {
            backgroundImage: `url(${vm.album?.blurPicUrl})`,
          };
        });
      vm.axios
        .get("https://apis.netstart.cn/music/comment/album", {
          params: {
            id: to.query.id,
          },
        })
        .then((res) => {
          console.log(res);
          vm.comments = res.data;
        });
      vm.axios
        .get("https://apis.netstart.cn/music/album/artists", {
          params: {
            id: to.query.id,
          },
        })
        .then((res) => {
          // console.log(res);
          vm.song = res.data.songs;
        });
    });
  },
  computed: {
    text() {
      return this.album?.description.split("\n")[0];
    },
    context() {
      return this.album?.description
        .split("\n")
        .filter((e) => e != this.album?.description.split("\n")[0]);
    },
  },
};
</script>

