  overflow: hidden;
  position: relative;
  .fanhui {
    position: absolute;
    left: 10rem;
    font-size: 16rem;
    top: 0;
    color: white;
  }
  p {
    font-size: 17rem;
    z-index: 999;
  }
  div:nth-of-type(2) {
    width: 60%;
    color: white;
    height: 126rem;
  }
  div.mask {
    width: 126rem;
    height: 126rem;
    position: relative;
    margin: 0 20rem;
    box-sizing: border-box;
    .albumbg {
      position: absolute;
