        180deg,
        rgba(0, 0, 0, 0.2),
        rgba(0, 0, 0, 0)
      );
      position: absolute;
      left: 0;
      top: 0;
    }
    .red {
      color: white;
      font-size: 12rem;
      text-align: center;
      top: 10rem;
      width: 40rem;
      height: 17rem;
      z-index: 999;
      background-color: rgba(217, 48, 48, 0.8);
      border-radius: 0 17rem 17rem 0;
    }
    p {
      position: absolute;
      left: 0;
    }
    img {
      z-index: 999;
      width: 126rem;
      height: 126rem;
    }
  }
  div > div {
    // margin: 5rem 0;
    display: flex;
    align-items: center;
    z-index: 999;
    // width: 32rem;
    height: 32rem;
    position: relative;
    img {
      width: 32rem;
      border-radius: 50%;
    }
    img:nth-of-type(2) {
      position: absolute;
      left: 25rem;
      bottom: 0;
      width: 12rem;
