    }
    span {
      margin: 0 10rem;
      color: hsla(0, 0%, 100%, 0.7);
    }
  }
  .mark {
    width: 100%;
    filter: blur(20rem);
    height: 100%;
    position: absolute;
    transform: scale(1.2);
    // background-position: 20%;
    background-position: center;
    background-repeat: no-repeat;

    left: 0;

    z-index: -1;
  }
}
.context {
  margin-bottom: 20rem;
  height: 60rem;
  // overflow: auto;
  //   white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
  padding: 10rem;
  color: #999;
  p {
    // display: inline-block;
    // white-space: nowrap;
    // overflow: hidden;
    // text-overflow: ellipsis;
