<template>
  <div class="controlMusic">
    <!-- <audio
      :src="`https://music.163.com/song/media/outer/url?id= ${r.id}.mp3`"
      autoplay
    ></audio> -->
    <div class="fenge">
      <div class="leftCd">
        <img
          class="cdCard"
          :src="res.picUrl ? res.picUrl : res.al.picUrl"
          :class="{ active: autoplay }"
        />
        <div class="cdSinger">
          <span class="cdSingName">{{ res.name }}</span>
          <p class="cdSingerName">
            {{
              res?.song?.artists[0].name
                ? res?.song?.artists[0].name
                : res?.ar[0].name
            }}
          </p>
        </div>
      </div>
      <div class="rightBtn">
        <svg
          @click.stop="
            $emit('change-song', 'prev');
            listCard = false;
          "
          t="1664181247315"
          class="prev"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="2804"
          width="200"
          height="200"
        >
          <path
            d="M694.272 809.024l-295.808-286.848 295.36-274.752a32 32 0 0 0-43.616-46.848l-320 297.696a32 32 0 0 0-0.512 46.4l320 310.304a32.032 32.032 0 0 0 44.576-45.952"
            p-id="2805"
            fill="#8a8a8a"
          ></path>
        </svg>

        <svg
          v-if="!autoplay"
          @click.stop="$emit('song-play')"
          t="1664181356781"
          class="play"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="3224"
          width="200"
          height="200"
        >
          <path
            d="M384 752.288l299.68-231.552L384 289.152V752.32z m-64 65.152V224a32 32 0 0 1 51.552-25.312l384 296.704a32 32 0 0 1 0 50.656l-384 296.736A32 32 0 0 1 320 817.44z"
            p-id="3225"
            fill="#8a8a8a"
          ></path>
        </svg>

        <svg
          v-else
          @click.stop="$emit('song-pause')"
          t="1664258905023"
          class="stop"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="2810"
          width="200"
          height="200"
        >
          <path
            d="M352 192a32 32 0 0 0-32 32v608.832a32 32 0 1 0 64 0V224a32 32 0 0 0-32-32M672 192a32 32 0 0 0-32 32v608.832a32 32 0 1 0 64 0V224a32 32 0 0 0-32-32"
            p-id="2811"
            fill="#8a8a8a"
          ></path>
        </svg>

        <svg
          @click.stop="
            $emit('change-song', 'next');
            listCard = false;
          "
          t="1664181437728"
          class="next"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="3660"
          width="200"
          height="200"
        >
          <path
            d="M693.792 498.24l-320-297.664a32 32 0 0 0-43.584 46.848l295.36 274.752-295.84 286.848a31.968 31.968 0 1 0 44.544 45.92l320-310.272a31.968 31.968 0 0 0-0.48-46.4"
            p-id="3661"
            fill="#8a8a8a"
          ></path>
        </svg>

        <svg
          @click.stop="listCard = !listCard"
          t="1664278584578"
          class="list"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="3190"
          width="200"
          height="200"
        >
          <path
            d="M779.776 480h-387.2a32 32 0 0 0 0 64h387.2a32 32 0 0 0 0-64M779.776 672h-387.2a32 32 0 0 0 0 64h387.2a32 32 0 0 0 0-64M256 288a32 32 0 1 0 0 64 32 32 0 0 0 0-64M392.576 352h387.2a32 32 0 0 0 0-64h-387.2a32 32 0 0 0 0 64M256 480a32 32 0 1 0 0 64 32 32 0 0 0 0-64M256 672a32 32 0 1 0 0 64 32 32 0 0 0 0-64"
            p-id="3191"
            fill="#8a8a8a"
          ></path>
        </svg>
      </div>
    </div>
    <div class="botton">
      <input
        type="range"
        name=""
        id=""
        min="0"
        :max="duration"
        ref="input"
        :value="tmpCurrentTime"
        @input.stop="
          draging = true;
          jindutiao($event.target.value);
        "
        @change="
          draging = false;
          $emit('update-time', $event.target.value);
        "
      />
      <p class="gray"></p>
      <p class="red" :style="{ width: inputTime }"></p>
      <p class="ball" :style="{ left: inputTime }"></p>
      <!-- <span class="time">总时长: {{ duration }}</span> -->
      <!-- <span class="overtime">倒计时: {{ duration - currentTime }}</span> -->
    </div>
    <ul class="listCard" v-show="listCard">
      <!-- <li @click="listCard = false && !showView">×</li> -->
      <li>当前播放({{ list.length }})</li>
      <li
        v-for="l in list"
        :key="l.id"
        @click.stop="$emit('clickHandler', l)"
        :style="{ color: res.id == l.id ? 'red' : 'black' }"
      >
        {{ l?.name }}
        <span @click.stop="shanchu(l)">×</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["res", "duration", "currentTime", "autoplay", "list", "showView"],
  data: function () {
    return {
      tmpCurrentTime: this.currentTime,
      // minute: null,
      // second: null,
      listCard: false,
      draging: false,
    };
  },
  // methods: {
  //   hand(x) {
  //     console.log(x);
  //   },
  // },
  computed: {
    inputTime() {
      return (this.tmpCurrentTime / this.duration) * 100 + "%";
    },
  },
  created: function () {
    // this.minute = Math.floor((this.duration % 3600) / 60);
    // this.second = Math.floor((this.duration % 3600) % 60).toFixed(2);
  },
  methods: {
    shanchu: function (n) {
      this.$emit(
        "del-list",
        this.list.filter((e) => {
          return !(e.id == n.id);
        })
      );
    },
    jindutiao(n) {
      // console.log(n, this.draging);
      this.tmpCurrentTime = n;
    },
    // star: function (n) {
    //   "updata-list",
    //     this.list.filter((e) => {
    //       // return (e.id == n.id).style.color"red";
    //       if (e.id == n.id) {
    //         return (this.e.style.color = "red");
    //       }
    //     });
    // },
  },
  watch: {
    currentTime: function (n) {
      // console.log(n);
      if (!this.draging) {
        this.tmpCurrentTime = n;
      }
    },
    // draging(n) {
    //   console.log(n);
    // },
  },
};
</script>

<style lang="less" scoped>
@keyframes yuan {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.controlMusic {
  // visibility: hidden;
  width: 100%;
  height: 100rem;
  background-color: rgb(66, 63, 63);
  position: fixed;
  bottom: 0;
  // z-index: 55;
  .fenge {
    margin-top: 5rem;
    display: flex;
    justify-content: space-between;
    height: 65%;
    align-items: center;
    .leftCd {
      display: flex;
      align-items: center;
      margin-left: 15rem;
      width: 50%;
      .cdCard {
        width: 50rem;
        height: 50rem;
        border-radius: 50%;
        animation-name: yuan;
        animation-duration: 6s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
        animation-play-state: paused;
      }
      .active {
        // animation: name duration timing-function delay iteration-count direction
        //   fill-mode;
        animation-play-state: running;
      }
      .cdSinger {
        text-align: left;
        margin-left: 5rem;
        color: rgb(215, 219, 223);
        width: 70%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        // word-spacing: 2rem;
        .cdSingName {
          // flex-wrap: nowrap;
          // width: 10%;
          // overflow: hidden;
          // text-overflow: ellipsis;
          // white-space: nowrap;
          display: block;
          margin-bottom: 5rem;
        }
        .cdSingerName {
        }
      }
    }
    .rightBtn {
      margin-right: 15rem;
      svg {
        width: 30rem;
        height: 30rem;
        margin: 3rem;
      }
    }
  }
  .botton {
    // display: flex;
    // justify-content: center;
    width: 100%;
    // margin-left: 15rem;
    position: relative;
    // z-index: 0;
    input {
      // visibility: hidden;
      opacity: 0;
      // display: none;
      width: 101%;
      height: 25rem;
      position: absolute;
      top: 0rem;
      z-index: 99;
      // z-index: -1;
    }
    .gray {
      width: 100%;
      height: 3rem;
      background-color: gray;
      position: absolute;
      top: 10rem;
    }
    .red {
      // width: 10%;
      width: 100%;
      height: 3rem;
      background-color: rgba(255, 0, 0, 0.532);
      position: absolute;
      top: 10rem;
    }
    .ball {
      width: 10rem;
      height: 10rem;
      border-radius: 50%;
      background-color: black;
      position: absolute;
      top: 8rem;
      left: 0%;
    }
    .time {
      position: absolute;
      right: 25rem;
      bottom: 0rem;
    }
    .overtime {
      position: absolute;
      right: 25rem;
      top: 8rem;
    }
  }
  .listCard {
    position: absolute;
    width: 95vw;
    height: 50vh;
    bottom: 110rem;
    z-index: 10;
    right: 10rem;
    border-radius: 20rem;
    background-color: rgb(252, 252, 252);
    overflow-y: auto;
    box-shadow: 0 0 5rem 0 rgb(189, 189, 189);
    padding: 10rem 5rem;
    // overflow-y: hidden;
    &::-webkit-scrollbar{display:none};
    // z-index: 0;
    li {
      // position: relative;
      text-align: left;
      margin: 0 15rem;
      margin-top: 5rem;
      border-bottom: 1px solid rgb(232, 232, 232);
      z-index: 5;
      // margin: 0 10rem;
      font-size: 16rem;
      display: flex;
      justify-content: space-between;
      // flex-wrap: nowrap;
      // width: 100%;
      // overflow: hidden;
      // text-overflow: ellipsis;
      // white-space: nowrap;
      line-height: 32rem;
      span {
        padding-right: 5rem;
        font-size: 26rem;
      }
      &:nth-child(1) {
        font-size: 18rem;
        border: none;
        margin-bottom: 10rem;
        font-weight: 600;
        // color: red;
      }
    }
  }
}
</style>